<template>
    <div>
        <div class="BoxBtn">
            <div class="BoxBtn__BtnArea">
                <div class="Btn" @click="handleCamera($event)" title="Turn On/Turn Off Camera">
                    <camera-icon size="1.3x" class="icon-camera"></camera-icon>
                    <camera-off-icon size="1.4x" class="icon-camera-off"></camera-off-icon>
                </div>
                <div class="BoxBtn__BtnArea--Title">Cam</div>
            </div>
            <div class="BoxBtn__BtnArea">
                <div class="Btn" @click="handleMic($event)" title="Turn On/Turn Off Microphone">
                    <mic-icon size="1.3x" class="icon-microphone"></mic-icon>
                    <mic-off-icon size="1.4x" class="icon-microphone-off"></mic-off-icon>
                </div>
                <div class="BoxBtn__BtnArea--Title">Mic</div>
            </div>
            <div class="BoxBtn__BtnArea">
                <div class="Btn" @click="handleScreenShare($event)"
                title="Turn On/Off Share Screen">
                    <monitor-icon size="1.3x" class="icon-share-screen"></monitor-icon>
                    <airplay-icon size="1.4x" class="icon-share-screen-off"></airplay-icon>
                </div>
                <div class="BoxBtn__BtnArea--Title">Share</div>
            </div>
            <div class="BoxBtn__BtnArea">
                <div class="Btn" id="BtnChartBar" @click="handleChartBar($event)"  title="Turn Open/Close Screen Chart Bar">
                    <pie-chart-icon size="1.3x" class="icon-chartBar"></pie-chart-icon>
                    <bar-chart-2-icon size="1.4x" class="icon-chartBar-off"></bar-chart-2-icon>
                </div>
                <div class="BoxBtn__BtnArea--Title">Graph</div>
            </div>
            <div class="BoxBtn__BtnArea">
                <div class="Btn" @click="handleMaximaze($event)" 
                title="Maximaze/Minimaze Video Screen">
                    <maximize-icon size="1.3x" class="icon-maximaze"></maximize-icon>
                    <minimize-icon size="1.5x" class="icon-maximaze-off"></minimize-icon>
                </div>
                <div class="BoxBtn__BtnArea--Title">Max</div>
            </div>
        </div>
    </div>
</template>

<script>
import { 
    CameraIcon, 
    CameraOffIcon, 
    MicIcon, 
    MicOffIcon, 
    MonitorIcon, 
    PieChartIcon, 
    MaximizeIcon,
    MinimizeIcon,
    AirplayIcon,
    BarChart2Icon
} from 'vue-feather-icons'
export default {
    components:{
        CameraIcon,
        MicIcon,
        MonitorIcon,
        PieChartIcon,
        MaximizeIcon,
        CameraOffIcon,
        MicOffIcon,
        MinimizeIcon,
        AirplayIcon,
        BarChart2Icon
    },
    data() {
        return {
            isOpenGraph: false
        }
    },
    methods: {
        handleCamera(e){
            e.currentTarget.classList.toggle("off");
        },
        handleMic(e){
            e.currentTarget.classList.toggle("off");
        },
        handleScreenShare(e){
            e.currentTarget.classList.toggle("off");
        },
        handleMaximaze(e){
            e.currentTarget.classList.toggle("off");
            let BxLive = document.getElementById('BxLive');
            BxLive.classList.toggle('Maximaze');
        },
        handleChartBar(e){
            e.currentTarget.classList.toggle("off");

            this.isOpenGraph = !this.isOpenGraph
            this.$emit('open_graph', this.isOpenGraph)
            setTimeout(() => {
                this.isOpenGraph = false
            }, 500);
        }
    }
}
</script>